/* app.css - goto css for kevmetz */

:focus {
  outline-style: none;
}

:root {

  /*titles and background - blue*/
  --hue: 222;
  --sat: 40%;

  /* declare globals at root level to support SVG and XML */
  /* this is higher specificity than the body tag, or even the html tag */

  /* remove horizontal scroll bars */
  overflow-x: hidden;

  /* remove all margins and padding - page articles will add their own */
  margin: 0;
  padding: 0;
  border: none;

  /* add set of colors */
  --main-pink: #ffc0cb;

  --main-yellow: #EADB67;

  --page-pink: #faacbc;
  --page-blue: #2B3F7D;
  --page-green: #3e6331;

  --parent-pink: #f6a1b2;
  --parent-blue: #44527C;
  --parent-green: #4d733f;

  --child-pink: #f9cad4;
  --child-blue: #5F6D91;
  --child-green: #6c905e;


  --parent-button-face-pink: #f9cad4;
  --parent-button-face-blue: #c2f9fa;
  --parent-button-face-green: #d9fac2;

  --parent-button-inactive-blue: #79bac5;

  --parent-button-border-pink: #f9cad4;
  --parent-button-border-blue: #77e8fa;
  --parent-button-border-green: #77fa93;

  --hover-pink: #f9dae1;
  --hover-blue: #dae1f9;
  --hover-green: #e1f9da;

  --icon-border-grey: #cab3c6;
  --parent-border-grey: #998888;
  --parent-border: solid 1px var(--parent-border-grey);
  --child-border-grey: #aa90a0;
  --child-border: solid 1px var(--child-border-grey);

  --selected-white: #e2e2e2;
  --selected-pink: #f9cad1;
  --selected-blue: #cad1f9;

  --text-white: #dddddd;
  --text-yellow: #EADB67;
  --text-black: #151515;
  --text-grey: #999999;
  --text-orange: #ffbf47;
  --text-green: #A4F226;
  --text-blue: #3b629c;

  --hover-border-color: rgb(128, 128, 94);

  --bubble-border-pink: #e6679055;
  --bubble-border-blue: #6790e655;

  --sky-blue: rgb(136, 208, 244);
  --sky-blue2: rgb(44, 132, 176);
  --sky-orange: rgb(252, 189, 40);

  --sky-dkpurple2: rgb(6, 0, 19);
  --sky-dkpurple: rgb(62, 13, 75);
  --sky-red: rgb(200, 28, 28);

  --focus-outline-orange: #ffc966;
  --focus-glow-orange: #ffb833;

  /* set all text to off-black */
  /* color: var(--text-black); */
  color: var(--text-grey);
}

button {
  color: var(--text-black);
  /* set all button text to off-black - buttons have own color reference */
}

.app-background {
  /* app-background is the first layer - it sizes to the screen */
  /* has background image and is one big column */
  background-color: rgb(193, 182, 178);
  background-image: url("../img/zpos.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 100vh;
}

.app-layout {
  /* app-layout is the second layer - it sizes to app-background */
  /* has a transparent overlay to adjust color of background image */
  /* is the container for the app */
  background-color: #FFFFFF11;
  /* next 6 lines are magic code */
  /* is the foundation of this framework */
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex: 1;
  width: 100%;
  /*max-width:1440px could be added here*/
  z-index: 10;
}

/* app-header, app-main, and app-footer are inside app-layout respectively */
.app-header {
  background-color: rgba(0, 0, 0, 0.95);
  color: teal;
  /*height: 7rem;*/
  height: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  /*position: sticky;*/
  top: 0;
  width: 100%;
  z-index: 200;
}

.app-main {
  /*background-color: var(--main-yellow);*/
  /*background-color: rgba(0, 0, 0, .5);*/
  background-color: rgba(0, 0, 0, .2);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex: 1;
  width: 100%;
  max-width: 1200px;
  /*z-index: 20;*/
}

.app-footer {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  align-content: flex-start;
  flex-wrap: no wrap;

  background-color: black;
  color: teal;
  width: 100%;
  z-index: 100;
}

.color-rotation {
  /* recommend using rgb(0, 255, 200) as base color/stroke/fill */
  /*  animation-name: logo-hue2;
  animation-duration: 32s;*/
  animation-name: logo-hue3;
  animation-duration: 15s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes logo-hue2 {
  0% {
    filter: hue-rotate(0deg)
  }

  50% {
    filter: hue-rotate(360deg)
  }

  100% {
    filter: hue-rotate(0deg)
  }
}

@keyframes logo-hue3 {
  0% {
    filter: hue-rotate(0deg)
  }

  50% {
    filter: hue-rotate(150deg)
  }

  0% {
    filter: hue-rotate(0deg)
  }
}

/* page-section is the top parent inside app-main */
.page-section {
  /*background-color: var(--page-green);*/
  background-color: #00000000;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /*align-items: center;*/
  /* for some reason aligning the items negates width:100% ?? */
  flex: 1;
  width: 100%;
  max-width: 960px;
}

/* both parent-article and child-article are just styling */
/* they style the content divs and don't format the layout */
/* they are children of page-section */
.parent-article {
  /*background-color: var(--parent-green);*/
  /*background-color: rgba(0, 0, 0, .5);*/
  background-color: rgba(0, 0, 0, .2);
  border: var(--parent-border);
  border-radius: 0.5rem;
  padding: 0.25rem 0.5rem;
  margin: 0.5rem 0.25rem;
  z-index: 30;
}

.child-article {
  /*background-color: var(--child-green);*/
  background-color: #000000;
  border: var(--child-border);
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin-top: .25rem;
  margin-bottom: .25rem;
  margin-left: .25rem;
  margin-right: .25rem;
}

.parent-button {
  padding: 1rem;
  border-radius: 1rem;
  border: solid 3px var(--parent-button-border-green);
  font-size: 1.2rem;
  margin-left: 0.25rem;
  margin-right: 0.25rem;
  cursor: pointer;
  background-color: var(--parent-button-face-green);
  /*box-shadow: 0px 0px 18px white;*/
  box-shadow: 0px 0px 18px rgb(157, 255, 0);
}

.svg-arrow {
  width: 2.4rem;
  fill: var(--parent-button-border-green);
  stroke: var(--parent-button-border-green);
  margin-left: 0.25rem;
  margin-right: 0.25rem;
}

/* wrap element in hover wrapper and give the element a class */
/* add hover selector class to the element class */
/* can be used for group hover by wrapping a parent div */
/* classes of children in the div will be selected as :hover */
.hover-wrapper {
  cursor: pointer;
}

.hover-wrapper:hover .svg-arrow {
  fill: var(--selected-white);
  stroke: var(--selected-white);
}

.hover-wrapper:hover .parent-button {
  background-color: var(--selected-white);
  box-shadow: 0px 0px 18px rgba(157, 255, 0, 0);
  border: solid 3px var(--hover-border-color);
}

.hover-wrapper:hover .child-article {
  outline: solid 2px var(--hover-border-color);
}

.hover-wrapper:hover .toppings-checkmark {
  border: solid 3px var(--hover-border-color);
}

/* ------- layout specific ------------ */

.toppings-img2 {
  border-radius: 2em;
  border: var(--child-border);
  width: 2em;
  height: 2em;
}

.toppings-img3 {
  border-radius: 3em;
  border: solid 3px var(--icon-border-grey);
  width: 3em;
  height: 3em;
}

.toppings-checkmark {
  border-radius: 2em;
  border: solid 3px rgb(202, 179, 198);
  width: 2em;
  background: var(--seleced-white);
  cursor: pointer;
}

.number-circle {
  border: 1px solid black;
  border-radius: .75rem;
  background-color: rgb(250, 172, 188);
  padding: 0.25rem;
}

.logo-header {
  position: absolute;
  height: 18rem;
  padding-bottom: 0rem;
  top: -8.5rem;
  left: -2.6rem;
  animation-name: logo-hue;
  animation-duration: 32s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes logo-hue {
  0% {
    filter: hue-rotate(170deg)
  }

  45% {
    filter: hue-rotate(190deg)
  }

  50% {
    filter: hue-rotate(200deg)
  }

  55% {
    filter: hue-rotate(190deg)
  }

  100% {
    filter: hue-rotate(170deg)
  }
}

.logo-footer {
  /*height: 3rem;*/
  height: 4rem;
  margin: 0.5rem;
  border: solid 0px none;
}

.logo-footer2 {
  height: 3.5rem;
  width: 3.5rem;
  margin: 0.5rem;
  border: solid 3px currentColor;
  border-radius: 2rem;
  cursor: pointer;
}

.logo-footer2:hover {
  outline-style: solid;
  outline-width: 2px;
  outline-color: var(--focus-outline-orange);
  box-shadow: 0px 0px 1.25rem var(--focus-glow-orange);
}

.three-sizer-container {
  visibility: hidden;
  height: 0;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

.three-canvas-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.titanium {
  background-image: url("../img/titanium.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.kevmetz {
  color: black;
  padding: 0.5rem;
  margin: 1rem;
  border: solid 3px var(--parent-border-grey);

  border-radius: 1rem;
  font-size: 1.6rem;
}

.kevmetz:hover {
  /*color: var(--hover-green);*/
  color: var(--hover-blue);
  cursor: pointer;
}

.info-img {
  max-height: 5rem;
  padding: 1rem;
}

.info-card {
  flex: 1;
  min-width: 17rem;
  max-width: 30rem;
}

.weather-icon-small {
  max-width: 6rem;
  border: solid 2px var(--icon-border-grey);
  border-radius: 3rem;
}


.btn-day-select {
  height: 64px;
  width: 64px;
  margin: 0.25rem;
}

.thermo-section {
  display: grid;
  grid-template-columns: 60px;
  align-self: flex-end;
  background: #0c69bb33;
  border-top-right-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  padding-top: 3px;
  padding-bottom: 3px;
  z-index: 50;
  color: white;
  justify-items: center;
}

.weather-detail {
  position: absolute;
  align-self: flex-start;
  width: 214px;
  height: 174px;

  background: #00000089;
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  z-index: 50;
  color: white;
  color: var(--text-white);
  /*justify-content: space-evenly;*/
  justify-content: center;
  cursor: pointer;
}

.badge-mode {
  display: flex;
  padding: 0.3rem;
  border-radius: 0.5rem;
  border: solid 2px var(--parent-button-border-blue);
  margin-left: 0.2rem;
  margin-right: 0.2rem;
  cursor: pointer;
  background-color: var(--parent-button-inactive-blue);
  color: var(--text-blue);
  box-shadow: none;
  text-align: center;
  align-items: center;
  justify-content: center;
  user-select: none;
}

.badge-active {
  /*box-shadow: 0px 0px 18px rgb(157, 255, 0);*/
  box-shadow: 0px 0px 18px white;
  color: var(--text-black);
  background-color: var(--parent-button-face-blue);
}

.day-detail {
  color: var(--text-white);
  background: #3e799955;
}

.trans-blue {
  background: #3e799933;
}

.trans-zero {
  background: #00000000;
  border: none;
}

.button-blue {
  box-sizing: content-box;
  font-size: 1rem;
  padding: 0.25rem;
  border-radius: 0.25rem;
  border: solid 2px var(--parent-button-border-blue);
  cursor: pointer;
  background-color: var(--parent-button-face-blue);
  color: var(--text-black);
  box-shadow: none;
  text-align: center;
  user-select: none;
  z-index: 80;
  /*margin-bottom: 1rem;*/
  width: 8rem;
}

.button-blue:focus {
  outline-style: solid;
  outline-width: 2px;
  outline-color: var(--focus-outline-orange);
  box-shadow: 0px 0px 1.25rem var(--focus-glow-orange);
}

.button-blue:hover {
  outline-style: solid;
  outline-width: 2px;
  outline-color: var(--focus-outline-orange);
  box-shadow: 0px 0px 1.25rem var(--focus-glow-orange);
}

.text-input-blue {
  font-size: 1rem;
  border: solid 2px var(--parent-button-border-blue);
  border-radius: 0.25rem;
  padding: 0.25rem;
  width: 10rem;
  z-index: 80;
}

.text-input-blue:focus {
  outline-style: solid;
  outline-width: 2px;
  outline-color: var(--focus-outline-orange);
  box-shadow: 0px 0px 1.25rem var(--focus-glow-orange);
}

.text-label-blue {
  background-color: var(--parent-button-inactive-blue);
  color: var(--text-blue);
  padding: 0.25rem;
  font-size: 1rem;
  border: solid 2px var(--parent-button-inactive-blue);
  width: 7rem;
  z-index: 80;
  user-select: none;
  cursor: pointer;
}

.text-label-blue2 {
  background-color: var(--parent-button-inactive-blue);
  color: var(--text-blue);
  padding: 0.25rem;
  font-size: 1rem;
  border: solid 2px var(--parent-button-inactive-blue);
  width: 6rem;
  z-index: 80;
  user-select: none;
  cursor: pointer;
}

.modal-wait {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 500;
  width: 100vw;
  height: 100vh;
  background: #00000000;
}

.abracadabra {
  background-color: #00000000;
}

@media screen and (min-width: 1216px) {
  .abracadabra {
    background: #3e799933;
  }
}