.flex-row {
  /*dont add anything*/
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  /*never use width:100% here, use align-items:stretch*/
}

.flex-col {
  /*dont add anything*/
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  /*never use height:100% here, use align-items:stretch*/
}

.wrap {
  flex-wrap: wrap;
}

.nowrap {
  flex-wrap: nowrap;
}

.row-top {
  align-items: flex-start;
}

.row-bottom {
  align-items: flex-end;
}

.row-middle {
  align-items: center;
}

.row-stretch {
  align-items: stretch;
}

.row-left {
  justify-content: flex-end;
}

.col-left {
  align-items: flex-start;
}

.col-right {
  align-items: flex-end;
}

.col-center {
  align-items: center;
}

.col-middle {
  justify-items: center;
}

.img-hv-flip {
  -moz-transform: scale(-1, -1);
  -o-transform: scale(-1, -1);
  -webkit-transform: scale(-1, -1);
  transform: scale(-1, -1);
}

.img-h-flip {
  -moz-transform: scale(-1, 1);
  -o-transform: scale(-1, 1);
  -webkit-transform: scale(-1, 1);
  transform: scale(-1, 1);
}

.img-v-flip {
  -moz-transform: scale(1, -1);
  -o-transform: scale(1, -1);
  -webkit-transform: scale(1, -1);
  transform: scale(1, -1);
}

.img-no-flip {
  -moz-transform: scale(1, 1);
  -o-transform: scale(1, 1);
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
}

