.satContainer {
  display: flex;
  position: relative;
  height: 41px;
  width: 41px;
  background: rgb(214, 19, 188, 0);
  font-size: 1rem;
  color: rgb(0, 208, 255);
  border: none;
  pointer-events: none;
  outline-style: none;
}

@keyframes targetTest {
  from {
    background-color: red;
  }

  to {
    background-color: yellow;
  }
}

@keyframes targetRotate {
  from {
    /*transform: rotate(1deg);*/
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.satTarget {
  position: absolute;
  height: 35px;
  width: 35px;
  background: rgba(233, 146, 53, 0.1);
  font-size: 1rem;
  color: lime;
  /*border: 3px dashed rgb(145, 255, 0);*/
  /*border: 3px dashed rgb(0, 255, 255);*/
  border: 3px dashed rgb(233, 146, 53);
  border-radius: 121px;
  left: 0px;
  top: 20px;
  animation-name: targetRotate;
  animation-duration: 45s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.satIcon {
  position: absolute;
  left: -100px;
  top: -90px;
  z-index: 90;
  height: 130px;
}

.satPulse {
  position: absolute;
  left: -40px;
  top: -30px;
  height: 80px;
  filter: blur(1px);
  filter: hue-rotate(200deg);
  opacity: 50%;
}